.card__container {
    width: 250px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    padding: 10px;
    color: black;
    text-decoration: none;
    position: relative;
}

.card__container:hover .button {
    display: block;
}

.card__container img {
    padding: 10px;
    width: 100%;
    height: 200px;
    object-fit: contain;
}

.container div:not(subtotal) {
    height: auto !important;
}

/*mouse hovering on add to cart color change to orange*/
.button {
    display: none;
    padding: 5px 10px; 
    font-weight: 700;
    border: none;
    cursor: pointer;
    background-color: var(--primary-color);
    width: 100%;
    margin: 10px 0;
    border-radius: 30px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.button:hover {
    background-color: var(--primary-shade);
}

.rating {
    display: flex;
    align-items: center;
    padding: 10px 0px;
}

.products__container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); /* fixed grid */
    max-width: 1450px;
    margin: 100px auto;
    gap: 50px;
    justify-content: center;
}

.products__container section {
    max-resolution: 0 auto;
}

.product__flexed {
    box-shadow: none;
    height: auto;
    display: flex;
    gap: 50px;
    width: 100%;
}


.product__flexed h3 {
    padding: 20px 0px;
}

.product__flexed .button {
    display: block;
    position: static;
    width: 150;
    margin-right: 40px;
}

.product__flexed {
    height: 50vh;
}

.product__flexed img {
    width: 300px !important;
    height: auto;
}

@media screen and (max-width: 768px) {
    .product__flexed {
        display: flex;
        flex-direction: column;
    }
}

.card__container:hover .button {
  display: block;
}

.button {
  display: none;
  padding: 10px 16px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  background-color: var(--primary-color);
  color: #111;
  width: 100%;
  margin: 10px 0;
  border-radius: 30px;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: var(--primary-shade);
}
